.dialog {
    width: 75%;
    border-radius: 0px 8px 8px 0px;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.5);
}

.header {
    height: 15%;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
    z-index: 2;
}

.fon {
    height: 30%;
    background-color: #005475;
    border-radius: 0px 8px 0px 0px;
}

.pomoshnikSearch {
    margin-top: 2px;
    height: 40%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
}

.pomoshnik {
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    align-items: center;
}

.iconBack:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 50%;
    cursor: pointer;
}

.search {
    -webkit-appearance: none;
    /* Удаляет стандартные стили WebKit */
    width: 10vw;
    /* Регулирует ширину */
    height: 50%;
    /* Регулирует высоту */
    border-bottom: 1px solid #ccc;
    /* Добавляет границу */
}

.spanPomoshnik {
    align-self: flex-start;
    font-family: "Montserrat";
    font-size: 12px;
    position: relative;
    font-weight: 600;
}

.spanPomoshnik span {
    color: rgb(58, 58, 58);
}

.spanPomoshnik::after {
    content: 'СТАТИСТИКА';
    position: absolute;
    left: 0;
    bottom: -15px;
    font-weight: 600;
    font-size: 12px;
}

.editText {
    padding: 0.1% 2%;
    height: 30%;
    width: 100%;
    background-color: #ffff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    column-gap: 2%;
}

.five {
    margin-left: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 15px;
}

.iconAdd {
    position: relative;
}

.iconAdd:hover::after {
    content: "Создать";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    bottom: -120%;
    left: -120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.iconSave {
    position: relative;
}

.iconSave:hover::after {
    content: "Сохранить";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    bottom: -120%;
    left: -120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.image:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
}

.image {
    height: auto;
    cursor: pointer;
    margin-right: 0.5%;
}

.main {
    position: relative;
    width: 100%;
    flex: 1 0 120px;
    max-height: 80vh;
    background-color: #ffff;
    padding-top: 1%;
    padding-bottom: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1%;
    overflow-y: auto;
}


.blockArrrow {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
    margin-top: 80px;
    align-self: flex-start;
}

.block1 {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;

}

.block2 {
    width: 99%;
    display: flex;
    min-height: 60px;
    flex-direction: row;
    column-gap: 5px;
}

.block3 {
    height: auto;
    width: 99%;
    display: flex;
    flex-direction: column;
    column-gap: 5px;
    border-top: solid 3px #B4B4B4;
    min-height: 150px;
}

.row2 {
    height: 75%;
    min-height: 75%;
    width: 99%;
    min-width: 99%;

    & textarea {
        width: 100%;
        min-width: 100%;
        height: 95%;
        min-height: 95%;
        padding: 15px;
        resize: none;
        overflow-y: auto;
    }
}

.row1 {
    height: 25%;
    width: 99%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    column-gap: 5px;
}

.element {
    font-family: "Montserrat";
    width: 23%;
    max-width: 23%;
    height: 25px;
    border-bottom: solid 1px #B4B4B4;
}


.addPoint {
    width: 30px;
    height: 50px;
    border: solid 2px #B4B4B4;

    &:hover {
        cursor: pointer;
        background-color: #8a8989;
    }

    & img {
        margin-top: 10px;
    }
}

.deletePoint {
    width: 30px;
    height: 50px;
    margin-left: auto;
    border: solid 2px #B4B4B4;
    text-align: center;

    &:hover {
        cursor: pointer;
        background-color: #8a8989;
    }

    & img {
        margin-top: 12px;
    }
}


.points {
    width: 73vw;
    display: flex;
    flex-direction: row;
    column-gap: 5px;
    overflow-x: auto;

    & .item {
        
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 90px;
        height: 50px;
        min-width: 90px;
        min-height: 50px;
        border: solid 2px #B4B4B4;
        text-align: center;

        & .date {
            height: 24px;
            border-bottom: solid 2px #B4B4B4;
            padding: 2px;
            padding-left: 8px;
            font-size: 14px !important;
        }

        & .number {
            height: 24px;
            padding: 2px;
            text-align: center;
        }
    }
}

.textGrey {
    font-family: "Montserrat";
    font-size: 13px;
    color: grey;
    text-align: center;
    margin-top: 2px;
}

.modal {
    position: fixed;
    z-index: 2000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.modalTable {
    position: relative;
    left: 10%;
    min-height: 300px;
    width: 410px;
    background-color: #fff;
    border-radius: 10px;
    border-collapse: collapse;
    display: block;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.6);
    padding: 5px;
}

.exitImage {
    position: absolute;
    float: right;
    top: -27px;
    right: -27px;
    cursor: pointer;
}

.modalTable thead,
.modalTable tbody {
    display: block;
    width: 400px;
    border-radius: 10px;
}


.modalTable thead tr {
    display: flex;
    column-gap: 5px;
}

.modalTable tbody {
    width: 400px;
    max-height: 65vh;
    margin-top: 5px;
    border-radius: 10px;
}

.modalTable th {
    width: 200px;
    padding: 10px;
    white-space: nowrap;
    vertical-align: top;
}

.modalTable td {
    width: 200px;
    display: flex;
    row-gap: 5px;
    flex-direction: column;
    overflow-y: auto;
    font-family: 'Montserrat';
    white-space: nowrap;
    vertical-align: top;
}

.modalTable tbody tr {
    display: flex;
    column-gap: 5px;
}

.modalTable thead th {
    color: #005475;
    font-family: 'Montserrat';
    background-color: #DDEBF1;
    position: sticky;
    top: 0;
    z-index: 1;
    border-radius: 10px;

}

.row {
    height: 25px;
    width: 100%;
    overflow: hidden;
    padding: 5px;
    display: flex;
    align-items: center;
    column-gap: 10px;
    background-color: #f0f3f5;
    border-radius: 10px;

    & input {
        background-color: #f0f3f5;
    }
}


.tableHeader {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;

    & span {
        font-family: 'Montserrat';
        font-size: 14px;
        font-weight: 600;
        margin-bottom: 10px;
    }
}

.iconSaveModal {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    align-self: flex-end;
    margin: 10px;
}

.iconSaveModal:hover::after {
    content: "Сохранить";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    top: 35px;
    right: -20px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.itemHover {
    cursor: pointer;
}

.active {
    border: solid 5px #005475 !important;
}

.item {
    max-width: 200px;
    overflow: hidden;
}

.itemName span {
    font-style: italic;
    font-size: 12px !important;
    font-family: 'Montserrat';
    font-weight: 400;
}

.select {
    position: relative;
    font-family: 'Montserrat';
    max-width: 200px;

    & option {
        font-family: 'Montserrat';
        max-width: 200px;
    }
}

.div {
    position: relative;
}

.div:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgb(240, 240, 240);
    z-index: 100;
}

.modalDelete {
    position: fixed;
    z-index: 2000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.modalDeleteElement {
    position: relative;
    height: 120px;
    width: 300px;
    left: 10%;
    top: -5%;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    padding: 10px;
}

.modalRow1 {
    display: flex;
    flex-direction: row;
    min-width: 280px;
}

.modalRow2 {
    display: flex;
    flex-direction: row;
    column-gap: 5px;
    margin-left: auto;
    margin-right: 5px;
}

.btnYes {
    height: 25px;
    width: 40px;
    border-radius: 10px;
    background-color: #005475;
    transition: background-color 0.3s;

    &:hover {
        background-color: #02445e;
    }
}

.btnNo {
    height: 25px;
    width: 40px;
    border-radius: 10px;
    background-color: #ffff;
    border: 1px solid #005475;
    transition: background-color 0.3s;

    &:hover {
        background-color: #eff3f5;
    }
}

.textBtnYes {
    font-weight: 400;
    font-family: 'Montserrat';
    color: #ffff;
    font-size: 14px;
}

.textBtnNo {
    font-weight: 400;
    font-family: 'Montserrat';
    color: #005475;
    font-size: 14px;
}

.text {
    font-weight: 400;
    font-family: 'Montserrat';
    color: rgb(58, 58, 58);
    font-size: 14px;
    line-height: 16px;
}

.textMontserrat {
    font-family: 'Montserrat';
}

.tableHeading {
    display: flex;
    flex-direction: row;
    column-gap: 5px;
    justify-content: center;
    align-items: center;
}

.tableHintWrapper {
    margin-bottom: 7px;
    display: inline-block;
    /* Обеспечивает, что обертка занимает размер содержимого */
    position: relative;
    /* Создаем контекст позиционирования */
}

.tableHintWrapper::after {
    display: none;
    /* Скрыто по умолчанию */
    content: "Это  значение будет влиять, только на тип графика (ежемесячный или годовой).";
    position: absolute;
    bottom: 100%;
    /* Расположить под элементом */
    left: 100%;
    width: 120px;
    /* Подгоните под нужный размер */
    padding: 5px;
    background-color: #696969;
    color: white;
    border-radius: 5px;
    z-index: 10;
    font-family: "Montserrat";
    font-size: 12px;
    font-weight: 400;
    text-align: center;
}

.tableHintWrapper:hover::after {
    display: block;
    /* Показываем всплывающее сообщение при наведении */
}